body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Gotham';
  src: local('Gotham'), url(./fonts/GothamLight.ttf) format('truetype');
}

@font-face {
  font-family: 'Gotham';
  src: local('Gotham'), url(./fonts/GothamBook.ttf) format('truetype');
}

@font-face {
    font-family: 'Gotham';
    font-weight: 900;
    src: local('Gotham'), url(./fonts/GothamMedium.ttf) format('truetype');
}

@font-face {
    font-family: 'Gotham';
    font-weight: 900;
    src: local('Gotham'), url(./fonts/GothamBold.ttf) format('truetype');
}